// MODAL LICENCIAS EXISTENTES
// Función para mostrar el modal confirmación de licencias existentes
function mostrarModalConfirmacion(mensaje) {
    return new Promise((resolve, reject) => { // Devuelve una nueva promesa
        const modal = document.getElementById("modalConfirmacion");
        const texto = document.getElementById("textoConfirmacion");
        const btnCancelar = document.getElementById("btnCancelar");
        const btnCrear = document.getElementById("btnCrear");
        const span = document.querySelector("#modalConfirmacion .close");
    
        console.log("El modal de confirmación se muestra");
    
        texto.innerText = mensaje;
        modal.style.display = "block";
    
        btnCancelar.onclick = function() {
            modal.style.display = "none";
            reject(new Error("Operación cancelada por el usuario")); // El usuario elige "Cancelar", rechaza la promesa
        };
    
        btnCrear.onclick = function() {
            modal.style.display = "none";
            resolve(true); // El usuario elige "Crear", resuelve la promesa
        };
    
        span.onclick = function() {
            modal.style.display = "none";
        };
    
        window.onclick = function(event) {
            if (event.target == modal) {
                modal.style.display = "none";
            }
        };
    });
}


// COMPROBAR ÚLTIMA LICENCIA EXISTENTE
// Asumiendo que el prefijo es conocido y que los IDs de los documentos siguen el formato 'prefijoN'
async function obtenerUltimaLicencia(kodigoLicencia) {
    const licenciasRef = collection(db, "licencias");
    // No aplicamos filtros aquí porque asumimos que todos los documentos en 'licencias' siguen el patrón
    const querySnapshot = await getDocs(licenciasRef);
    let maxNum = 0;

    querySnapshot.forEach(doc => {
        const id = doc.id;
        // Intentamos extraer el número después del prefijo
        const match = id.match(`^${kodigoLicencia}(\\d+)$`);
        if (match) {
            const num = parseInt(match[1], 10);
            if (num > maxNum) maxNum = num;
        }
    });

    return maxNum;
}




