// CARGAR DATOS DE CENTROS EDUCATIVOS DESPLEGABLES - AGREGAR CENTRO
const cargarCentrosEducativos = async () => {
    const selectCentroEducativo = document.getElementById("selectCentroEducativo");
    const selectCiudad = document.getElementById("selectCiudad");
    const selectIdCentro = document.getElementById("selectIdCentro");
	const selectIdClase = document.getElementById("selectIdClase");

    try {
        const querySnapshot = await getDocs(collection(db, "centros"));
        let centrosUnicos = new Set();

        querySnapshot.forEach(doc => {
            const data = doc.data();
            centrosUnicos.add(data.centroeducativo);
        });

        centrosUnicos.forEach(centro => {
            const option = document.createElement("option");
            option.value = centro;
            option.textContent = centro;
            selectCentroEducativo.appendChild(option);
        });

        selectCentroEducativo.addEventListener('change', async () => {
            const centroSeleccionado = selectCentroEducativo.value;
            selectCiudad.innerHTML = '<option value="">Seleccione una ciudad...</option>';
			selectIdCentro.innerHTML = '<option value="">Seleccione un ID de centro...</option>';
			selectIdClase.innerHTML = '<option value="">ID de clase existentes</option>';
            
            const ciudadesQuery = await getDocs(query(collection(db, "centros"), where("centroeducativo", "==", centroSeleccionado)));
            let ciudades = [];

            ciudadesQuery.forEach(doc => {
                const { nombreciudad, idCentro } = doc.data();
                ciudades.push({ nombreciudad, idCentro });
            });

            ciudades.forEach(({ nombreciudad }) => {
                const option = document.createElement("option");
                option.value = nombreciudad;
                option.textContent = nombreciudad;
                selectCiudad.appendChild(option);
            });
        });

        selectCiudad.addEventListener('change', async () => {
            const ciudadSeleccionada = selectCiudad.value;
            const centroSeleccionado = selectCentroEducativo.value;
            
            const idCentroQuery = await getDocs(query(collection(db, "centros"), where("centroeducativo", "==", centroSeleccionado), where("nombreciudad", "==", ciudadSeleccionada)));
            idCentroQuery.forEach(doc => {
                const { idCentro } = doc.data();
                selectIdCentro.innerHTML = `<option value="${idCentro}">${idCentro}</option>`; // Rellena y selecciona automáticamente el idCentro
            });
			cargarIdClase(selectIdCentro.value);
        });
		
		// Podríamos necesitar también escuchar cambios en selectIdCentro en caso de que tenga múltiples opciones
		selectIdCentro.addEventListener('change', () => {
			cargarIdClase(selectIdCentro.value);
		});

    } catch (error) {
        console.error("Error al cargar los datos de Firestore:", error);
    }
};

// Función para cargar los idClase basada en el idCentro seleccionado - AGREGAR CENTRO
const cargarIdClase = async (idCentroSeleccionado) => {
	console.log(`idCentroSeleccionado: ${idCentroSeleccionado}`);
    const selectIdClase = document.getElementById("selectIdClase");
    selectIdClase.innerHTML = '<option value="">ID de clase existentes</option>'; // Resetea el desplegable de ID de clase

    try {
		// Convierte idCentroSeleccionado a número
        const idCentroNumerico = Number(idCentroSeleccionado);
        // Asegúrate de que tu consulta a la colección "licencias" esté correcta
        const licenciasQuery = await getDocs(query(collection(db, "licencias"), where("idCentro", "==", idCentroNumerico)));
		console.log(`Documentos encontrados: ${licenciasQuery.size}`); // Ver cuántos documentos se recuperan
		
		let idsClaseUnicos = new Set(); // Utiliza un Set para almacenar valores únicos de idClase
        licenciasQuery.forEach(doc => {
            const { idClase } = doc.data();
            idsClaseUnicos.add(idClase); // Añade el idClase al Set, lo que automáticamente elimina duplicados
        });

        idsClaseUnicos.forEach(idClase => { // Itera sobre el Set de idsClaseUnicos
            const option = document.createElement("option");
            option.value = idClase;
            option.textContent = idClase;
            selectIdClase.appendChild(option); // Añade opciones al select para cada idClase único
        });


        // Si solo hay una opción, podríamos querer seleccionarla automáticamente aquí
        if (idsClaseUnicos.size === 1) {
            selectIdClase.selectedIndex = 1; // Selecciona automáticamente si solo hay una opción
        }

    } catch (error) {
        console.error("Error al cargar los ID de clase desde Firestore:", error);
    }
};


// SCRIPTS PARA AGREGAR DOCENTE
async function generarYAgregarDocente() {
    const nombredocente = document.getElementById("nombredocente").value;
    const emaildocente = document.getElementById("emaildocente").value;
    const centroEducativodocente = document.getElementById("selectCentroEducativoDocente").value;
    const idCentrodocente = parseInt(document.getElementById("selectIdCentroDocente").value, 10);
    const idClasedocente = document.getElementById("selectNuevoIdClaseDocente").value;
    const ciudaddocente = document.getElementById("selectCiudadDocente").value;
    const anoExpiraciondocente = document.getElementById("anofechaexpiracionDocente").value;
	const licenciaprefijodocente = document.getElementById("licenciaprefijodocente").value;
	const licenciageneradadocente = document.getElementById("licenciageneradadocente");
	// Construyo el nuevo nombre de licencia generado automáticamente
	const kodigoLicenciadocente = "Profe" + idClasedocente + licenciaprefijodocente + anoExpiraciondocente;
	console.log("Este es el nuvo prefijo: ", kodigoLicenciadocente);
	
	// Validación de otros campos requeridos
    if (!nombredocente || !emaildocente || !centroEducativodocente || isNaN(idCentrodocente) || !idClasedocente || !ciudaddocente || !anoExpiraciondocente ||!licenciaprefijodocente) {
        mostrarModalRequisitosDocente("Error", "Por favor, asegúrate de rellenar todos los campos requeridos correctamente.");
        return;
    }
	
    // Validación del prefijo de la licencia
    if (!licenciaprefijodocente || licenciaprefijodocente.length < 4) {
        mostrarModalRequisitosDocente("Error", "El licencia prefijo debe tener al menos 4 caracteres.");
        return;
    }

	const existeLicenciaDocente = await comprobarExistenciaLicenciaDocente(kodigoLicenciadocente);
	if (existeLicenciaDocente) {
		try {
			// Espera aquí hasta que el usuario responda
			await mostrarModalExistenciaDocente(`La licencias ${kodigoLicenciadocente} ya está en uso.`);
			return;
		} catch (error) {
			console.log(error.message); // Manejar el rechazo, posiblemente mostrando un mensaje o log
			return; // Importante: Detiene la ejecución de la función si el usuario cancela
		}
	}

    // Generación y agregación de docente
    const licenciaProfeRef = doc(db, 'profesores', kodigoLicenciadocente);
    let fechaExpiracionDateDocente = new Date(`${anoExpiraciondocente}-09-30T12:00:00Z`);
    fechaExpiracionDateDocente.setHours(fechaExpiracionDateDocente.getHours() - 1);
    const fechaExpiracionTimestampDocente = Timestamp.fromDate(fechaExpiracionDateDocente);

    try {
        await setDoc(licenciaProfeRef, {
			nombre: nombredocente,
			email: emaildocente,
            centro: centroEducativodocente,
            idCentro: idCentrodocente,
            idClase: idClasedocente,
            licencia: kodigoLicenciadocente,
            fechaexpiracion: fechaExpiracionTimestampDocente
        });
    } catch (error) {
        console.error(`Error al agregar la licencia ${kodigoLicenciadocente}: `, error);
    }

    mostrarLicenciaModalDocente("Licencia creada correctamente:", kodigoLicenciadocente);
	// Si pasa todas las validaciones, procede a generar el PDF
    //generarPDFdocente(nombredocente, emaildocente, centroEducativodocente, idCentrodocente, idClasedocente, ciudaddocente, anoExpiraciondocente, kodigoLicenciadocente);
	document.getElementById('botonCerrarModalLicenciaDocente').addEventListener('click', function() {
		// Llama a la función de generar PDF después de que el modal se cierra
		generarPDFdocente(nombredocente, emaildocente, centroEducativodocente, idCentrodocente, idClasedocente, ciudaddocente, anoExpiraciondocente, kodigoLicenciadocente);
	});
}

