// COMPROBAR LICENCIA EXISTENTE DOCENTE
async function comprobarExistenciaLicenciaDocente(kodigoLicenciadocente) {
    const licenciasRef = collection(db, "profesores");
    const q = query(licenciasRef, where("licencia", "==", kodigoLicenciadocente));
    const querySnapshot = await getDocs(q);

    if (querySnapshot.empty) {
        console.log("No existe un documento con el código de licencia proporcionado.");
        return false; // Retorna false para indicar que no se encontró el documento
    } else {
        console.log("Existe un documento con el código de licencia proporcionado.");
        querySnapshot.forEach(doc => {
            // Aquí puedes manejar los datos del documento encontrado
            console.log(doc.id, " => ", doc.data());
        });
        return true; // Retorna true para indicar que se encontró al menos un documento
    }
}

