const cargarIdClaseDocente = async (idCentroSeleccionadoDocente) => {
	console.log(`idCentroSeleccionadoDocente: ${idCentroSeleccionadoDocente}`);
    const selectIdClaseDocente = document.getElementById("selectIdClaseDocente");
    selectIdClaseDocente.innerHTML = '<option value="">ID de clase existentes</option>'; // Resetea el desplegable de ID de clase

    try {
		// Convierte idCentroSeleccionadoDocente a número
        const idCentroNumericoDocente = Number(idCentroSeleccionadoDocente);
        // Asegúrate de que tu consulta a la colección "profesores" esté correcta
        const profesoresQuery = await getDocs(query(collection(db, "profesores"), where("idCentro", "==", idCentroNumericoDocente)));
		console.log(`Documentos encontrados: ${profesoresQuery.size}`); // Ver cuántos documentos se recuperan
		
		let idsClaseUnicosDocente = new Set(); // Utiliza un Set para almacenar valores únicos de idClase
        profesoresQuery.forEach(doc => {
            const { idClase } = doc.data();
            idsClaseUnicosDocente.add(idClase); // Añade el idClase al Set, lo que automáticamente elimina duplicados
        });

        idsClaseUnicosDocente.forEach(idClase => { // Itera sobre el Set de idsClaseUnicosDocente
            const option = document.createElement("option");
            option.value = idClase;
            option.textContent = idClase;
            selectIdClaseDocente.appendChild(option); // Añade opciones al select para cada idClase único
        });


        // Si solo hay una opción, podríamos querer seleccionarla automáticamente aquí
        if (idsClaseUnicosDocente.size === 1) {
            selectIdClaseDocente.selectedIndex = 1; // Selecciona automáticamente si solo hay una opción
        }

    } catch (error) {
        console.error("Error al cargar los ID de clase desde Firestore:", error);
    }
};

