// PDF LICENCIAS
function generarPDFlicencias(centroEducativo, numeroLicencias, idClase, ciudad, anoExpiracion, nombresLicenciasCreadas) {
	console.log("Estamos en la función generar pdf");
	// Objeto para mapear idClase a Clasedocente
	const clases = {
		"4a": "4º ESO | Grupo A",
		"4b": "4º ESO | Grupo B",
		"4c": "4º ESO | Grupo C",
		"4d": "4º ESO | Grupo D",
		"4e": "4º ESO | Grupo E",
		"1BachA": "1º Bachillerato | Grupo A",
		"1BachB": "1º Bachillerato | Grupo B",
		"1BachC": "1º Bachillerato | Grupo C",
		"1BachD": "1º Bachillerato | Grupo D",
		"1BachE": "1º Bachillerato | Grupo E",
		"2BachA": "2º Bachillerato | Grupo A",
		"2BachB": "2º Bachillerato | Grupo B",
		"2BachC": "2º Bachillerato | Grupo C",
		"2BachD": "2º Bachillerato | Grupo D",
		"2BachE": "2º Bachillerato | Grupo E"
	  // Agrega más mapeos según sea necesario
	};
	
	const Clasedocente = clases[idClase];
	
	if (window.jspdf && window.jspdf.jsPDF) {
		const { jsPDF } = window.jspdf;
		const doc = new jsPDF();
		// Obtener la fecha actual
		const fecha = new Date();
		const fechaActual = fecha.getDate() + "/" + (fecha.getMonth() + 1) + "/" + fecha.getFullYear();
		const xInicial = 15; // La posición inicial X para imprimir las licencias
		let x = xInicial; // La posición X actual que se irá actualizando
		const maxPorLinea = 4; // Máximo de nombres de licencia por línea
		const stepX = 45; // El paso horizontal después de cada nombre de licencia
		let y = 230; // Inicia la impresión en la posición Y = 20
		const stepY = 10; // El paso vertical después de cada línea de licencia
				
		// Añadir una imagen 'imagenBase64' con cadena base64
        const imagenChica64 = '';
		doc.addImage(imagenChica64, 'JPEG', 130, 90, 50, 50);
		
		const imagenLogo64 = '';
		doc.addImage(imagenLogo64, 'PNG', 0, 0, 70, 55);

		// Añadir un encabezado
        doc.setFontSize(18);
        doc.text('Detalle de Licencias Alumnos', 65, 30); // Cambia las coordenadas según sea necesario
		doc.setFontSize(12);
		doc.text(`¡Combina juego y enseñanza | Crea entorno educativo!`, 55, 40);
		// Añadir texto al documento
		doc.setFontSize(12)
		doc.text(`${fechaActual}`, 170, 15);
		doc.text(`¡Hola ${centroEducativo}! bienvenido al SaaS Neodogma donde los alumnos aprendrán`, 15, 60);
		doc.text(`las bases de la actual Biología Molecular, con términos de Genética y Biotecnología.`, 15, 70);		
		doc.text(`Estos son los datos que hemos registrado:`, 15, 90);
		doc.text(`* Número de licencias: ${numeroLicencias}`, 15, 100);
		doc.text(`* Centro Educativo: ${centroEducativo}`, 15, 110);
		doc.text(`* Clase: ${Clasedocente}`, 15, 120);
		doc.text(`* Ciudad: ${ciudad}`, 15, 130);
		doc.text(`* Expiración licencias: 30 Sept ${anoExpiracion}`, 15, 140);
		doc.text(`¡Ahora podéis descargar nuestra app y registrar cada licencia con cada alumno`, 15, 160);
		doc.text(`* Recuerda, que cada licencia solo puede ser registrada en un único dispositivo.`, 15, 170);
		doc.text(`Podéis descargar nuestra app Dogma en las siguientes plataformas:`, 15, 180);
		doc.text(`* Google Play: https://play.google.com/store/apps/details?id=com.Youmu.Dogma.android&hl=es_419`, 15, 190);
		doc.text(`* App Store: https://apps.apple.com/es/app/dogma/id6476204721`, 15, 200);
		doc.text(`Licencias generadas:`, 15, 220);
		
		nombresLicenciasCreadas.forEach((nombreLicencia, index) => {
			// Imprime el nombre de la licencia en la posición actual
			doc.text(nombreLicencia, x, y);

			// Verifica si es momento de saltar a la próxima línea o continuar en la misma
			if ((index + 1) % maxPorLinea === 0) {
				y += stepY; // Avanza hacia abajo para empezar una nueva línea
				x = xInicial; // Restablece X a la posición inicial para la nueva línea
			} else {
				x += stepX; // Avanza a la derecha para el siguiente nombre en la misma línea
			}

			// Si 'y' es mayor que el límite de la página, agrega una nueva página y resetea 'y' y 'x'
			if (y > 280) {
				doc.addPage();
				y = 20; // Resetea Y al inicio para la nueva página
				x = xInicial; // Restablece X a la posición inicial para la nueva página
			}
		});

		// Previsualizr el PDF
		const blob = doc.output("blob");
		previsualizarPDFlicencias(blob);
		
		// Guardar el PDF
		//doc.save('Licencia-Docente.pdf');
	} else {
		console.error('jsPDF no está disponible.');
	}
}

