// Previsualizar el PDF DOCENTE en una página web
function previsualizarPDFdocente(blob) {
    // Crea una URL para el blob
    const url = URL.createObjectURL(blob);

    // Opción 1: Abrir el PDF en una nueva pestaña del navegador
    window.open(url);
}

// Previsualizar el PDF LICENCIAS en una página web
function previsualizarPDFlicencias(blob) {
    // Crea una URL para el blob
    const url = URL.createObjectURL(blob);

    // Opción 1: Abrir el PDF en una nueva pestaña del navegador
    window.open(url);
}

